import React, { useState } from 'react';
import { MapContainer, Tooltip, GeoJSON, TileLayer, LayerGroup, LayersControl, Marker, ZoomControl } from 'react-leaflet';
import L from 'leaflet';
import 'leaflet/dist/leaflet.css';
import mapData from '../assets/mapData.json';
import cardData from '../assets/cardData.json'
import './Map.css';

function GetFlagIcon() {
    const _iconSize = 30
    return L.icon({
        iconUrl: require("../assets/icon.png"),
        iconAnchor: [_iconSize / 2, _iconSize],
        iconSize: [_iconSize]
    })
}

let regionStyle = {
    fillColor: "#FFD500",
    fillOpacity: 0.4,
    weight: 2
}

let onEachRegion = (region, layer) => {
    const name = region.properties.name_1;
    const type = region.properties.type_1;
    if (type === "Independent City") {
        layer.bindPopup(`${name}`);
    } else {
        layer.bindPopup(`${name} ${type}`);
    }
}

function Map() {
    const [data, setData] = useState({ id: '', latlng: '', title: '', image: '', body: [] })
    const [isInfoCardVisible, setIsInfoCardVisible] = useState(false)

    function renderCard(e) {
        for (let i = 0; i < cardData.length; i++) {
            if (cardData[i].id === e.target.options.id) {
                setData({
                    title: cardData[i].title,
                    image: cardData[i].image,
                    body: cardData[i].body
                })
                setIsInfoCardVisible(!isInfoCardVisible)
                return
            }
        }
        console.warn("Unable to display the card: No data was provided")
    }

    const toggleMarker = {
        click: (e) => {
            renderCard(e);
        }
    }

    const toggleInfoCard = () => {
        setIsInfoCardVisible(!isInfoCardVisible)
        if (!isInfoCardVisible) {
            setData({
                title: 'Title',
                image: '',
                body: 'text'
            })
        }
    }

    return (
        <div>
            <section
                className={`info-card-container ${isInfoCardVisible ? 'active' : ''}`}
                onClick={toggleInfoCard}
            >
                <article className={`info-card ${isInfoCardVisible ? 'active' : ''}`}>
                    <div className="shadow-top" />
                    <div className='info-card-content'>
                        <h2>{data.title}</h2>
                        <img src={data.image} alt="" />
                        <p>
                            {
                                data.body.map((paragraph, id) => {
                                    return (
                                        <span
                                            key={id}
                                            style={{ display: 'block', minHeight: '16px' }}
                                        >
                                            {paragraph}
                                        </span>
                                    )
                                })
                            }
                        </p>
                    </div>
                    <div className="shadow-bottom" />
                </article>
            </section>
            <MapContainer
                className='map'
                preferCanvas={true}
                zoom={5}
                minZoom={5}
                center={[49.01, 31.28]}
            >
                <LayersControl position='bottomleft'>
                    <ZoomControl position='bottomright'></ZoomControl>
                    <LayersControl.Overlay checked name='Ukrainian Map'>
                        <GeoJSON
                            style={regionStyle}
                            data={mapData.features}
                            onEachFeature={onEachRegion}
                        >
                        </GeoJSON>
                    </LayersControl.Overlay>
                    <LayersControl.Overlay checked name='World Map'>
                        <TileLayer
                            attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
                            url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
                        />
                    </LayersControl.Overlay>
                    <LayersControl.Overlay checked name="Markers">
                        <LayerGroup>
                            {
                                cardData.map((location => {
                                    return (
                                        <Marker
                                            id={location.id}
                                            key={location.id}
                                            position={location.latlng}
                                            icon={GetFlagIcon()}
                                            eventHandlers={toggleMarker}
                                        >
                                            <Tooltip
                                                direction="top"
                                                opacity={1}
                                                offset={[0, -30]}
                                                className='location-tooltip'
                                            >
                                                <img src={location.image} alt="" />
                                                <p>{location.title}</p>
                                            </Tooltip>
                                        </Marker>
                                    )
                                }))
                            }
                        </LayerGroup>
                    </LayersControl.Overlay>
                </LayersControl>
            </MapContainer>
        </div>
    );
}

export default Map
